<template>
    <div class="login-and-logout">
        <div v-if="!isLoggedIn" class="login">
            <div class="login__form-wrapper">
                <v-form v-model="form.isValid" ref="login_form" class="login__login-form">
                    <v-text-field
                        v-model="form.login"
                        :rules="rules.login"
                        label="Login"
                        required outlined
                        :autocomplete="Math.random()"
                        @keyup.native.enter="login"
                    ></v-text-field>
                    <v-text-field
                        v-model="form.password"
                        :rules="rules.password"
                        :type="isHiddenPassword ? 'password' : 'text'"
                        label="Password"
                        required outlined
                        :autocomplete="Math.random()"
                        @keyup.native.enter="login"
                    >
                        <template v-slot:append>
                            <i :class="eyeIconClass"
                               class="login__eye-icon"
                               @click="onEyeClick"
                            ></i>
                        </template>
                    </v-text-field>
                    <v-btn
                        class="login__submit-button"
                        color="primary"
                        large
                        @click="login"
                    >Submit</v-btn>

                </v-form>
                <div v-if="error" class="login__error">{{ error }}</div>
            </div>
        </div>
        <div v-else class="logout">
            <h1>You are already logged in.</h1>
            <h1>If you'd like to, you can</h1>
            <v-btn
                class="logout__button"
                color="primary"
                x-large depressed tile
                @click="logout"
            >Log out</v-btn>
        </div>
    </div>
</template>

<script>
import appBarTitleMixin from '@/mixins/appBarTitleMixin';
import { mapGetters } from 'vuex';
import { RouterNames } from '@/constants/RouterNames';

export default {
    name: "Login",
    mixins: [
        appBarTitleMixin,
    ],
    data() {
        return {
            form: {
                isValid: false,
                login: '',
                password: '',
            },
            rules: {
                login: [
                    login => !!login || 'Login is required',
                ],
                password: [
                    password => !!password || 'Password is required',
                ],
            },
            isHiddenPassword: true,
            error: '',
        }
    },
    computed: {
        ...mapGetters(['isLoggedIn']),
        appBarTitle() {
            return 'Login Page';
        },
        eyeIconClass() {
            const off = this.isHiddenPassword ? '' : '-off';
            return 'mdi mdi-eye' + off;
        }
    },
    methods: {
        onEyeClick() {
            this.isHiddenPassword = !this.isHiddenPassword;
        },
        login() {
            const loginFormElement = this.$refs.login_form;
            const isFormValid = loginFormElement.validate();
            if (!isFormValid) {
                return;
            }
            this.$store.commit('validateCredentials', {
                login: this.form.login,
                password: this.form.password,
            });
            if (!this.isLoggedIn) {
                this.error = 'The provided credentials are invalid';
                return;
            }
            this.$router.replace({ name: RouterNames.Hello });
        },
        logout() {
            this.$store.commit('logOut');
            this.$router.replace({ name: RouterNames.Hello });
        }
    }
}
</script>

