    margin-top: 100px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    &__form-wrapper {
        width: 400px;
        margin-top: 30px;

    }
    &__login-form {
        & > * {
            margin-bottom: 20px;
        }
        margin-bottom: 30px;
    }
    &__eye-icon {
        cursor: pointer;
        transform: scale(1.5);
    }
    &__submit-button {
        width: 100%;
    }
    &__error {
        color: red;
    }
}

.logout {
    margin-top: 30px;
    display: flex;
    flex-direction: column;
    align-items: center;
    &__button {
        margin-top: 20px;
        width: 300px;
    }
}

</style>
